@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Open Sans", sans-serif;
}

.asteroids-base {
  display: flex;
  flex-wrap: wrap;
  margin: 35px;
  justify-content: center;
}

.asteroids-base > div {
  margin: 10px;
}

.asteroids-info {
  width: 265px;
  height: 200px;
  background: #10699c;
  color: #fff;
}

.asteroids-info a {
  color: #fff;
}
